import App from "./App.svelte";
import Cpu from "./model/Cpu";
import Ram from "./model/Ram";
import SymbolTable from "./model/SymbolTable";
import Wires from "./model/Wires";
import { cpuStore, programExecutionLoopStore, programExecutionStore, ramStore, symbolTableStore, wiresStore } from "./store/state";
import { cpu as cpuComponentStore, ram as ramComponentStore, wires as wiresComponentStore, stepText as stepTextComponentStore } from "./store/components";
import { init as initSettings } from "./store/settings";
import { init as initText } from "./store/text";
import ProgramExecution from "./execution/ProgramExecution";
import NonBlockingLoop from "./execution/NonBlockingLoop";
import { storage } from "./util/localStorage";
import { exportProgram, parseProgram } from "./util/programParser";
var ram = new Ram();
var cpu = new Cpu();
var symbolTable = new SymbolTable();
var wires = new Wires();
ramStore.set(ram);
cpuStore.set(cpu);
symbolTableStore.set(symbolTable);
wiresStore.set(wires);
// when a label is edited in the symbol table, all its occurences in the ram are synched
symbolTable.addLabelEditedListener(function (event) { return ram.updateLabel(event.oldLabel, event.newLabel); });
// when a label is deleted from the symbol table, all its occurences in the ram are replaced by the address that label was mapped to
symbolTable.addLabelRemovedListener(function (event) { return ram.removeLabel(event.removedLabel); });
// loads the program stored in local storage into the ram (restores previous session)
var program = parseProgram(storage.getOrElse("program", "NOP"));
ram["import"](program.ram);
symbolTable["import"](program.symbolTable);
// when the state of the ram or of the symbol table is altered, the program stored in them is saved to local storage
ram.instructions.subscribe(function (newState) { return storage.set("program", exportProgram(ram, symbolTable)); });
symbolTable.labels.subscribe(function (newState) { return storage.set("program", exportProgram(ram, symbolTable)); });
initSettings();
initText();
var programExecution = new ProgramExecution(cpu, ram, symbolTable, wires);
var programExecutionLoop = new NonBlockingLoop(programExecution, 20);
programExecutionStore.set(programExecution);
programExecutionLoopStore.set(programExecutionLoop);
var app = new App({
    target: document.body,
    props: {
        cpu: cpu,
        ram: ram,
        symbolTable: symbolTable,
        programExecution: programExecution,
        initExecution: initExecution
    }
});
export default app;
function initExecution() {
    var cpuComponent = cpuComponentStore.get();
    var ramComponent = ramComponentStore.get();
    var wiresComponent = wiresComponentStore.get();
    var stepTextComponent = stepTextComponentStore.get();
    if (!cpuComponent)
        throw new Error("cpu component not yet initialized");
    if (!ramComponent)
        throw new Error("ram component not yet initialized");
    if (!wiresComponent)
        throw new Error("wires component not yet initialized");
    if (!stepTextComponent)
        throw new Error("step text component not yet initialized");
    programExecution.executionContext.cpu.component = cpuComponent;
    programExecution.executionContext.ram.component = ramComponent;
    programExecution.executionContext.wires.component = wiresComponent;
    programExecution.executionContext.stepTextComponent = stepTextComponent;
    programExecutionLoop.start();
}
